<script setup lang="ts">
import { ref } from 'vue'
import type { PropType } from 'vue'
import type { PageConfig } from '@/types'
import { ChevronDown, ChevronUp } from 'lucide-vue-next'

const props = defineProps({
  config: {
    type: Object as PropType<PageConfig>,
    required: true
  }
})

const expandedItems = ref<Record<number, boolean>>({})

// Initialize all collapsible items as expanded or collapsed? Let's default to collapsed.
// Actually, for better UX, maybe first one expanded? Let's keep all collapsed for clean look.

const toggleItem = (index: number) => {
  if (props.config.itemStyle === 'collapsible') {
    expandedItems.value[index] = !expandedItems.value[index]
  }
}
</script>

<template>
  <div class="h-full w-full flex flex-col items-center justify-center bg-slate-50 p-8">
    <div class="max-w-4xl w-full h-full flex flex-col">
      <div class="mb-8 text-center shrink-0">
        <h2 class="text-3xl font-bold text-slate-900 mb-2">{{ config.title }}</h2>
        <p v-if="config.subTitle" class="text-slate-500">{{ config.subTitle }}</p>
      </div>

      <div class="flex-1 min-h-0 overflow-y-auto">
        <div class="space-y-4 pb-4">
          <div 
            v-for="(item, index) in config.items" 
            :key="index"
            class="bg-white rounded-xl border border-slate-200 overflow-hidden transition-all duration-200"
            :class="{ 'shadow-sm': !expandedItems[index], 'shadow-md ring-1 ring-blue-100': expandedItems[index] }"
          >
            <button 
              class="w-full flex items-center justify-between p-5 text-left outline-none hover:bg-slate-50 transition-colors"
              @click="toggleItem(index)"
              :class="{ 'cursor-default': config.itemStyle !== 'collapsible' }"
            >
              <h3 class="font-bold text-slate-800 text-lg">{{ item.title }}</h3>
              
              <div v-if="config.itemStyle === 'collapsible'" class="text-slate-400">
                <ChevronUp v-if="expandedItems[index]" class="w-5 h-5" />
                <ChevronDown v-else class="w-5 h-5" />
              </div>
            </button>

            <div 
              v-if="config.itemStyle !== 'collapsible' || expandedItems[index]"
              class="px-5 pb-5 text-slate-600 leading-relaxed border-t border-slate-100 pt-4"
            >
              {{ item.description }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>